<template>
    <div class="container">
        <v-back title="照片上传" />
        <md-field>
            <md-input-item
              ref="input0"
              title="学生学号"
              placeholder="请输入学号"
              is-amount
              :maxlength="15"
              v-model="usernbr">
                <div class="input-operator" slot="right" @click="search">查询</div>
            </md-input-item>
            <div v-show="show">
                <div class="form_banner border-bottom flex">
                    <div>学生姓名</div>
                    <div class="content">{{detail.username}}</div>
                </div>
                <div class="form_banner border-bottom flex">
                    <div>所在班级</div>
                    <div class="content">{{detail.college_name}}{{detail.org_names}}{{detail.class_names}}</div>
                </div>
                <div class="form_banner border-bottom flex">
                    <div>学生图片</div>
                    <div class="content">
                        <img :src="detail.photo" alt="">
                    </div>
                </div>
                <div class="form_banner border-bottom flex">
                    <div>上传图片</div>
                </div>
                <md-notice-bar>
                    <md-icon slot="left" class="md-notice-demo-icon md-notice-demo-icon-left" name="security"></md-icon>
                    已有的学生图片会被替换
                </md-notice-bar>
                <ul class="image-reader-list">
                    <li
                    class="image-reader-item"
                    v-for="(img, index) in imageList['tupian']"
                    :key="index"
                    :style="{
                        'backgroundImage': `url(${img})`,
                        'backgroundPosition': 'center center',
                        'backgroundRepeat': 'no-repeat',
                        'backgroundSize': 'cover'
                    }">
                    <md-tag
                        class="image-reader-item-del"
                        size="small"
                        shape="quarter"
                        fill-color="#111A34"
                        type="fill"
                        font-color="#fff"
                        @click.native="onDeleteImage('tupian', index)"
                    >
                        <md-icon name="close"></md-icon>
                    </md-tag>
                    </li>
                    <li class="image-reader-item add" v-show="imageList.tupian.length == 0">
                        <md-image-reader
                            name="tupian"
                            size="2048"
                            @select="onReaderSelect"
                            @complete="onReaderComplete"
                            @error="onReaderError"
                        >
                        </md-image-reader>
                        <md-icon name="camera" size="md" color="#CCC"></md-icon>
                        <p>添加图片</p>
                    </li>
                </ul>
                <md-field>
                    <md-button type="primary" size="small" round @click="submit">开始上传</md-button>
                </md-field>
            </div>
        </md-field>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'photo_upload',
        data() {
            return {
                imageList: {
                    tupian: []
                },
                show: false,
                usernbr: '',
                detail: {},
                blob: null,
                file: {}
            }
        },
        methods: {
            search() {
                if (this.usernbr == '') {
                    this.$toast.failed('请输入学号')
                } else {
                    this.$getL('/users/student', {
                        userId: JSON.parse(localStorage.getItem('user')).user_id,
                        usernbr: this.usernbr
                    }, (res)=>{
						this.detail = res.data
						this.$toast.hide()
						this.show = true
                    })
                }
                
            },
            onReaderSelect(name, {files}) {
                files.forEach(file => {
                    console.log('[Mand Mobile] ImageReader Selected:', 'File Name ' + file.name)
                })
                this.$toast.loading('图片读取中...')
            },
            onReaderComplete(name, {dataUrl, blob, file}) {
                this.$toast.hide()
                console.log('[Mand Mobile] ImageReader Complete:', 'File Name ' + file.name)
                setTimeout(() => {
                    const demoImageList = this.imageList[name] || []
                    demoImageList.push(dataUrl)
                    this.$set(this.imageList, name, demoImageList)
                    this.blob = blob
                    this.file = file

                }, 100)
            },
            onReaderError(name, {msg}) {
                this.$toast.failed(msg)
            },
            onDeleteImage(name, index) {
                const demoImageList = this.imageList[name] || []
                demoImageList.splice(index, 1)
                this.$set(this.imageList, name, demoImageList)
            },
            submit() {
                if (this.imageList.tupian.length == 0) {
                    this.$toast.failed('请上传图片')
                    return
                }
                var formData = new FormData();
                let config = {
                    headers: {'Content-Type': 'multipart/form-data'}
                }
				console.log(this.file.name)
                formData.append("file", this.blob, this.file.name);
                formData.append("usernbr", this.usernbr);
                this.$toast.loading('loading')
                axios.post(this.$baseUrl() + '/users/upload', formData, config).then( res => {
                   console.log(res.data)
                   this.updateStudent(res.data.data)
                })
            },
			updateStudent(photo) {
				this.$getL('/users/update_student', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id,
				    usernbr: this.usernbr,
					photo: photo
				}, (res)=>{
					console.log(res)
					this.$toast.succeed("上传成功")
				})
			},
        }
    }
</script>

<style scoped>
    .input-operator {
        background-color: #1acfc6;
        color: #fff;
        padding: 2vw 3vw;
    }
    .form_banner {
        font-size: 4.2vw;
        padding: 3vw 0;
    }
    .image-reader-list {
        float: left;
        width: 100%;
        margin-top: 3vw;
    }
    .image-reader-list .image-reader-item {
        position: relative;
        float: left;
        width: 25vw;
        height: 25vw;
        padding-bottom: 23.5%;
        margin-bottom: 2%;
        margin-right: 2%;
        background: #FFF;
        box-shadow: 0 5px 20px rgba(197, 202, 213, .25);
        box-sizing: border-box;
        list-style: none;
        border-radius: 4px;
        background-size: cover;
        overflow: hidden;
    }
    .image-reader-list .image-reader-item .md-tag {
        position: absolute;
        right: 0;
    }
    .image-reader-item.add .md-icon {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: .5;
    }
   .image-reader-item.add p {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        margin-top: 15px;
        font-size: 22px;
        color: #CCC;
        text-align: center;
        font-size: 12px;
    }
    .content {
        margin-left: 5vw;
    }
    .content img {
        width: 20vw;
        height: 20vw;
    }
</style>